import styles from './friendsitem.module.css'
import {NavLink} from 'react-router-dom';
import user from "../../../assets/Profile/usersProfileIcon.png";
import React from "react";

function FriendsItem(props) {
    return <div className={styles.Friends}>
        <div>
            <span>
                <div>
                    <NavLink to={"/user/" + props.id}>
                        <img src={props.photos.small != null ? props.photos.small : user} className={styles.logo}/>
                    </NavLink>
                </div>
                <div className={styles.logoContainer}>
                    {props.followed
                        ?
                        <button disabled={props.followingInProgress.some(id => id === props.id)}
                                className={styles.subscribe}
                                onClick={() => {
                                    props.unFollow(props.id)
                                }}>Unfollow</button>
                        :
                        <button disabled={props.followingInProgress.some(id => id === props.id)}
                                className={styles.subscribe}
                                onClick={() => {
                                    props.Follow(props.id)
                                }}>Follow</button>
                    }
                </div>
            </span>
            <div className={styles.description}>
                <span className={styles.descriptionContainer}>
                    <div className={styles.name}>
                        {props.name != null ? props.name : "Unknown User"}
                    </div>
                    <div className={styles.status}>
                        {props.status != null ? props.status : "I do not know what is the status meaning"}
                    </div>
                    </span>
                <span>
                    <div>
                        {"u.location.country"}, {"u.location.city"}
                    </div>
                </span>
            </div>
        </div>
    </div>
}

export default FriendsItem;