import React, {useEffect} from 'react'
import styles from "./friends.module.css";
import {useDispatch, useSelector} from "react-redux";
import {Follow, getUsers, SearchFriend, setCurrentPage, unFollow} from "../../redux/FriendsReducer";
import FriendsItem from "./FriendsItem/FriendsItem";
import SearchBoxFriends from "./SearchFriends/SearchBox";
import {getAllUsers, getCurrentPage, getPageSize, getProgressInFollowing, getTotalUsersCount} from "../../redux/FriendsSelector";
import {Pagination} from "antd";

function Friends() {
    const [users, totalUsersCount, pageSize, followingInProgress, currentPage] = useSelector(state => [
        getAllUsers(state), getTotalUsersCount(state), getPageSize(state),
        getProgressInFollowing(state), getCurrentPage(state)]);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getUsers(currentPage, pageSize));
    }, []);
    return (
        <div className={styles.Friends}>
            <div className={styles.Pagination}>
                <Pagination defaultCurrent={1} total={totalUsersCount} onChange={(pageNumber) => {
                    dispatch(setCurrentPage(pageNumber));
                    dispatch(getUsers(pageNumber));
                }} current={currentPage} pageSize={pageSize} showSizeChanger={false}/>
            </div>
            <div>
                <SearchBoxFriends SearchFriend={() => dispatch(SearchFriend())}/>
            </div>
            <div>
                {users.map(u => {
                    return <FriendsItem users={users} followingInProgress={followingInProgress}
                                        Follow={(id) => dispatch(Follow(id))} unFollow={(id) => dispatch(unFollow(id))}
                                        id={u.id} name={u.name} status={u.status} followed={u.followed}
                                        photos={u.photos} key={u.id}

                    />
                })}
            </div>

        </div>
    )
}

export default Friends;